<template>
  <div class="">
    <p>关于我</p>
    <ul>
      <li>screen-height: {{ sh }}</li>
      <li>screen-width: {{ sw }}</li>
      <li>body-height: {{ bh }}</li>
      <li>body-width: {{ bw }}</li>
      <li>useragent: {{ agent }}</li>
    </ul>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        sh: window.screen.height,
        sw: window.screen.width,
        bh: document.body.clientHeight,
        bw: document.body.clientWidth,

        agent: navigator.userAgent
      }
    }
  }
</script>
<style scoped>
  li{text-align: left;}
</style>



